<template>
    <div class="all_com">
        <div class="title_com">商品管理</div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>商品信息</span>
            </div>
            <div class='detail'>
                <div class='detail-item'>
                    <div class='item-left'>商品类型</div>
                    <div class='item-right'>{{form.Name}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>线上库存</div>
                    <div class='item-right'>{{form.DefaultAttr.OriStock}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>线下库存</div>
                    <div class='item-right'>{{form.DefaultAttr.OriOfflineStock}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>线上剩余库存</div>
                    <div class='item-right'>{{form.DefaultAttr.InStock}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>线下剩余库存</div>
                    <div class='item-right'>{{form.DefaultAttr.OfflineStock}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>重量</div>
                    <div class='item-right'>{{form.DefaultAttr.Weight}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>开售时间</div>
                    <div class='item-right'>{{form.PutAwayTime | date}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>商品详情 </div>
                    <div class='item-right'>
                        <el-button @click='detailLang("cn")' type="primary" size="mini">
                            中文
                        </el-button>
                        <el-button @click='detailLang("en")' type="primary" size="mini">
                            英文
                        </el-button>
                    </div>
                </div>
            </div>

            <div class='detail top-20' v-show='this.lang!=""'>
                <div class='detail-item'>
                    <div class='item-left'>{{la('商品名','Product Name')}}</div>
                    <div class='item-right'>{{la(form.Name,form.EnglishName)}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>{{la('商品描述','Description')}}</div>
                    <div class='item-right'>{{la(form.Description,form.EnglishDescription)}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>{{la('单价','Price')}}</div>
                    <div class='item-right'>{{la(form.DefaultAttr.Price,form.DefaultAttr.UsdPrice)}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>{{la('发货时间','Delivery Time')}}</div>
                    <div class='item-right'>{{form.DeliveryTime |date}}</div>
                </div>
                <div class='detail-item'>
                    <div class='item-left' style="border-right: none;">{{la('详情','Detail')}}</div>
                    <div class='item-right'  style="    border-left: 1px solid #ddd;" v-html='la(form.Details,form.EnglishDetails)'></div>
                </div>
                <div class='detail-item'>
                    <div class='item-left'>{{la('操作','Action')}}</div>
                    <div class='item-right'>
                        <el-button @click='edit(form.ID)' type="primary" size="mini">
                            {{la('编辑','edit')}}
                        </el-button>
                    </div>
                </div>
            </div>
        </el-card>

    </div>
</template>
<script>
    export default {
        name: "",
        data() {
            return {
                form: {
                    DefaultAttr:{

                    }
                },
                lang:''
            };
        },
        methods: {
            la(cn,en){
                if(this.lang == 'cn'){
                    return cn;
                }else if(this.lang == 'en'){
                    return en;
                }else{
                    return "";
                }
            },
            detailLang(type){
                this.lang = type;
            },
            getDetail(id){
                let params = {
                    product_id: id,
                };
                this.$api("/admin/product/info", params).then(res => {
                    this.form = res.data;
            });
            },
            edit(id){
                this.$router.push(`/productmanage/product_insert?id=${id}`)
            }
        },
        created() {
            this.getDetail(this.$route.params.id)
        }
    };
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .detaila:hover {
        color: #409eff;
        text-decoration: underline;
    }
    .top-20{
        margin-top: 20px;
    }
    .detail{
        display: inline-block;
    .detail-item{
        line-height: 40px;
        text-indent: 20px;
        font-weight: bold;
        float: left;
        width: 100%;
        border: 1px solid #ddd;
    }
    .detail-item:nth-of-type(odd) {
        background-color: #f9f9f9;
    }
    .detail-item:nth-of-type(n+2) {
        border-top: none;
    }
    .item-left{
        width: 200px;
        float: left;
        border-right: 1px solid #ddd;
    }
    .item-right{
        float: left;
    }
    }
    .search_fun {
        height: 60px;
        position: relative;
        top: 10px;
        float: right;
    }
    .search_input {
        width: 200px;
    }

</style>


